body {
  font-family: 'Montserrat', sans-serif;
  background:#a786df;
  margin: 0 auto;
  min-height: 100vh; /* Ingej ondooroo duurgene */
}

.App {
  text-align: center;
}

.App-header {
	letter-spacing:0.1em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: white;
  text-shadow: 
						8px 8px #f582ae,
						20px 20px #000000;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #172c66;
}

